@import url(https://fonts.googleapis.com/css2?family=Akaya+Telivigala&family=Roboto+Slab&display=swap);

.header {
    background-color: rgb(51, 51, 131);
    height: 300px;
    color: #fff;
}
.header-text {
    font-size: 45px;
    font-weight: bolder;
    letter-spacing: 3px;
    padding: 5.1rem 0 0 28.1rem;
}
.form {
    width: 30rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 40px auto;
}

.todo-form {
    width: 80%;
    height: 50px;
    padding: 10px;
    border-radius: 6px;
    font-size: 20px;
}
.todo-form:focus {
    outline: none;
}
.todo-form::-webkit-input-placeholder {
    font-size: 20px;
}
.todo-form:-ms-input-placeholder {
    font-size: 20px;
}
.todo-form::placeholder {
    font-size: 20px;
}

.todo-btn {
    width: 5rem;
    border-radius: 6px;
    border: 0;
    background-color:rgb(110, 110, 245);
    height: 48px;
}

@media screen and(max-width: 700px) {
    .header-text {
        left: 0;
    }
}

.todo-list {
    width: 90%;
    background-color: #fff;
    margin: 0 auto;
    padding: 15px;
    margin-bottom: 20px;
    border-radius: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.input-text {
    background-color: white;
    width: 80%;
    font-size: 20px;
}
.delete-btn {
    float: right;
}

.container {
    background-color: rgb(238, 144, 20);
    width: 30rem;
    margin: -40px auto;
    border-radius: 10px;
    padding: 30px;
    margin-bottom: 5px;

}
.list-text {
    text-align: center;
    margin-bottom: 20px;
    font-size: 30px;
    font-weight: bolder;
}
.input-checkbox {
    margin-top: 6px;
}

.active {
    text-decoration: line-through;
    width: 80%;
    background-color: #fff;
    color: red
}

@media screen and(max-width: 600px) {
    .container {
        width: 20rem;
    }
}
.footer {
    width: 30rem;
    margin: 0 auto;
    background-color: rgb(58, 58, 131);
    padding: 10px;
    border-radius: 10px;
    color: #fff;
    box-shadow: 2px 5px #888888;
}

.opt {
    cursor: pointer;

}
.opt:hover {
    font-size: 20px;
    color: chartreuse;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Akaya Telivigala', cursive;
}
